.filterBox {
  width: 100%;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.filterTitle {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
}

.filterInput {
  display: block;
  width: 100%;
  height: 35px;
  margin-top: 5px;
  padding-left: 12px;
  padding-left: 12px;
  border: 1px solid #dfe1e5;
  border-radius: 8px;
  outline: none;

  font-size: 16px;
  color: rgba(0, 0, 0, 0.8);
}

.filterInput:hover,
.filterInput:focus {
  box-shadow: rgba(50, 50, 105, 0.15) 0px 2px 5px 0px,
    rgba(0, 0, 0, 0.05) 0px 1px 1px 0px;
}
